<template>
  <el-container>
    <el-header>
      <v-head v-bind:title="head_title"></v-head>
    </el-header>
    <el-main>
      <div class="noice_title">{{form.type}} | {{form.title}}</div>
      <nobr style="font-weight: 600;margin-left: 10px;">{{form.author}}</nobr>
      <el-tag :key="tag" v-for="tag in keyWordList" class="keyword">{{tag}}</el-tag>
      <div class="main_text">{{form.text}}</div>
      <div class="bottom_text">
        <nobr :key="reference" v-for="reference in referenceList" > | {{reference}}</nobr>
      </div>
      <div class="bottom_text">
        <nobr :key='label' v-for="label in labelList" > | {{label}}</nobr>
      </div>
      <div style="text-align: center;" v-if="need_review">
        <el-button class="review_button" @click="review(1)">  </el-button>
        <el-button class="review_button" @click="review(2)"></el-button>
      </div>

      <div style="text-align: center;" v-else>
        <el-button class="review_choose" v-if="review_pass"><br>{{pass_num}}</el-button>
        <el-button class="review_button" v-else> <br>{{pass_num}} </el-button>
        <el-button class="review_button"  v-if="review_pass"><br>{{unpass_num}}</el-button>
        <el-button class="review_choose" v-else><br>{{unpass_num}}</el-button>

      </div>
    </el-main>
    <v-footer></v-footer>
  </el-container>
</template>

<script>
  import vHead from './common/Header.vue';
  import vFooter from './common/Footer.vue';
  export default {
    name: "Review",
    data() {
      return {
        head_title: " MicroKnowledge",
        referenceList: ['c', ''],
        keyWordList: ['machine learning', 'python'],
        labelList: ['', 'hhh'],
        need_review:true,
        review_pass:true,
        pass_num:1,
        unpass_num:3,
        form: {
          type: "",
          title: '',
          text: '       ',
          keyWord: '',
          label: '',
          reference: '',
          author: 'uc',
        },
      }
    },
    components: {
      vHead,
      vFooter
    },
    created() {
      console.log("init");
      this.getUserInfo();
    },
    methods: {
      async getUserInfo() {
        var params = new URLSearchParams();
        var id = this.$route.params.id;

        //--------------------------------------------------------
        //this.$route.params.id
        //idwjkid
        //urlauthor
        //--------------------------------------------------------
        //url
        //idid
        //need_reviewfalsetrue
        //review_pass
        //pass_num/unpass_num/
        //--------------------------------------------------------

        if (id > 0) {
          params.append('id', id);
          try {
            let res = await this.axios.post('/mEvidence/toModifyMEvid', params);
            console.log(res.data);
            this.form.title = res.data.title;
            this.form.text = res.data.summary;
            this.form.keyWord = res.data.keywords;
            this.form.label = res.data.topic;
            this.form.reference = res.data.citedPaper;
            this.form.author = res.data.author;
          } catch (err) {
            console.log(err);
          }
        } else {
          params.append('id', -id);
          try {
            let res = await this.axios.post('/mGuess/toModifyMGuess', params);
            console.log(res.data);
            this.form.title = res.data.title;
            this.form.text = res.data.summary;
            this.form.keyWord = res.data.keywords;
            this.form.label = res.data.topic;
            this.form.reference = res.data.citedEvidList;
          } catch (err) {
            console.log(err);
          }
        }
        this.referenceList = this.form.reference.split('-');
        this.keyWordList = this.form.keyWord.split('-');
        this.labelList = this.form.label.split('-');
      },


      review(type){
        this.need_review=false;
        if(type==1){
          this.review_pass=true;
          this.pass_num=this.pass_num+1;
        }
        else{
          this.review_pass=false;
          this.unpass_num=this.unpass_num+1;
        }
        //----------------------------------
        //url
        //----------------------------------
      }
    }

  }
</script>

<style>
  .el-header {
    background-color: #FFFFFF;
    line-height: 1.5;
  }

  .el-main {
    background-color: #F4F4F5;
    color: #333;
    padding-left: 15%;
    padding-right: 25%;
  }

  .noice_title {
    color: #409EFF;
    text-align: left;
    font-size: 25px;
    font-weight: 500;
    display: inline-block;
    margin: 10px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .main_text {
    margin: 10px;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .keyword {
    margin-left: 10px;
    border-radius: 30px;
    height: 23px;
    margin-right: 10px;
    border-radius: 30px;
  }
  .bottom_text{
    margin-left: 10px;
    color: slategrey;
    font-size:15px;
  }
  .review_button{
    width: 100px;
    height: 50px;
    margin: 30px;
  }
  .review_choose{
    width: 100px;
    height: 50px;
    margin: 30px;
    background-color: #ECF5FF;
    color: #409EFF;
    border: 1px solid #C6E2FF;
  }
</style>



