<template>
  <div class="notice">
    <div class="noice_title">
      {{type_name}} | {{title}}
      <el-button class="bottom_tag" @click="toDetail"></el-button>
    </div>
    <nobr style="font-weight: 600;margin-left: 10px;">{{authorName}}</nobr>
    <el-tag :key="tag" v-for="tag in keywordTag" class="keyword">{{tag}}</el-tag>
    <div class="main_text">{{summary}}</div>
    <el-button class="bottom_tag"></el-button>
    <el-button class="bottom_tag"></el-button>
    <el-button class="bottom_tag"></el-button>
    <el-button class="bottom_tag"></el-button>
    <el-button class="bottom_tag"></el-button>
    <el-button class="bottom_tag" v-if="user" @click="toModify"></el-button>
    <el-button class="bottom_tag" v-if="user" @click="toDelete"></el-button>
  </div>
</template>

<script>
  export default {
    name: "Notice",
    props: {
      id: {
        type: Number,
        default: 1
      },
      type: {
        type: Number,
        default: 1
      },
      authorName: {
        type: String,
        default: 'uc'
      },
      keywords: {
        type: String,
        default: 'machine learning-c++'
      },
      title: {
        type: String,
        default: ''
      },
      summary: {
        type: String,
        default: "     "
      },
      user: {
        type: Boolean,
        default: false
      },
    },

    data() {
      return {
        keywordTag: [],
        type_name: '',
      }
    },
    methods: {
      toModify() {
        this.$router.push({
          path: '/modify_mevid/',
          query: {
            id: this.id
          }
        });
      },
      toDetail() {
        this.$router.push({
          path: '/detail/',
          query: {
            id: this.id
          }
        });
      },

      toDelete() {
        var params = new URLSearchParams();
        params.append('id', this.id);
        this.axios.post('/mNotice/deleteNotice', params)
          .then((res) => {
            var remindTitle = res.data === 0 ? '' : '';
            var remindContent = res.data === 0 ? '' : '/(o)/~~';
            this.$alert(remindContent, remindTitle, {
              confirmButtonText: ''
            });
            if (res.data === 0) {
              this.$router.push('/homepage');
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    mounted() {
      this.keywordTag = this.keywords.split('-');
      if (this.type == 1) {
        this.type_name = "";
      } else {
        this.type_name = "";
      }
    },
  }
</script>

<style>
  .notice {
    border: 1px solid #409EFF;
    width: 99%;
    border-radius: 10px;
    margin-bottom: 10px;
  }

  .noice_title {
    color: #409EFF;
    text-align: left;
    font-size: 20px;
    font-weight: 500;
    display: inline-block;
    margin: 10px;
    margin-bottom: 5px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .main_text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    margin: 10px;
    margin-bottom: 1px;
  }

  .keyword {
    margin-left: 10px;
    border-radius: 30px;
    height: 23px;
  }

  .bottom_tag {
    margin: 10px;
    margin-right: 0;
    line-height: 7px;
    height: 25px;
  }
</style>



